import { toNano } from '@ton/core';

class PaymentService {
  private static SUBSCRIPTION_PRICE = 0.1; // 0.1 TON  
  private static SUBSCRIPTION_DURATION = 7 * 24 * 60 * 60 * 1000; // 7   
  private static OWNER_ADDRESS = 'UQAHmegvebyyP9bVXc-H1unVw9UrUZyPD15jB6PfYVM-ZcvJ'; //     
  private static apiEndpoint = 'https://api.ton.dev'; //    API- 
  private static apiKey = 'your-api-key'; //    API-

  //     
  static async createSubscriptionPayment() {
    try {
      //  BigInt  
      const amount = toNano(this.SUBSCRIPTION_PRICE.toString());
      
      //    
      const transaction = {
        validUntil: Math.floor(Date.now() / 1000) + 300, // 5   
        messages: [
          {
            address: this.OWNER_ADDRESS,
            amount: amount.toString(10), //  BigInt  
            stateInit: null,
            payload: ''
          },
        ],
      };

      console.log(' :', transaction);
      return transaction;
    } catch (error) {
      console.error('   :', error);
      throw error;
    }
  }

  //   
  static isSubscriptionActive(): boolean {
    const subscriptionData = localStorage.getItem('subscription');
    if (!subscriptionData) return false;

    const { expiresAt } = JSON.parse(subscriptionData);
    return Date.now() < expiresAt;
  }

  //     
  static activateSubscription() {
    const subscriptionData = {
      active: true,
      startedAt: Date.now(),
      expiresAt: Date.now() + this.SUBSCRIPTION_DURATION,
    };
    localStorage.setItem('subscription', JSON.stringify(subscriptionData));
  }

  //    
  static getSubscriptionInfo() {
    const subscriptionData = localStorage.getItem('subscription');
    if (!subscriptionData) return null;

    return JSON.parse(subscriptionData);
  }

  //   
  static async checkTransactionStatus(transactionHash: string): Promise<boolean> {
    try {
      //       TON API
      console.log(' :', transactionHash);
      const response = await fetch(`${this.apiEndpoint}/transactions/${transactionHash}`, {
        headers: {
          'Authorization': `Bearer ${this.apiKey}`,
          'Accept': 'application/json',
        }
      });

      if (!response.ok) {
        console.error('   :', await response.text());
        return false;
      }

      const data = await response.json();
      return data.status === 'completed';
    } catch (error) {
      console.error('   :', error);
      return false;
    }
  }

  //  
  static deactivateSubscription() {
    localStorage.removeItem('subscription');
  }
}

export default PaymentService; 